.page.navigation-open {
  @include overlay;

  .navigation {
    width: 320px;
    height: 100vh;
    background-color: $lcg-1;
    transition: width 0.3s, height 0.3s, background 0.3s;
  }

  .toggle__line {
    background: 0 0;

    &::before {
      transform: rotate3d(0, 0, 1, 45deg);
    }

    &::after {
      transform: rotate3d(0, 0, 1, -45deg);
    }

    &::before,
    &::after {
      top: 0;
      transition: top 0.3s, transform 0.3s 0.5s;
      background: $mcg-4;
    }
  }

  // .toggle__line {
