.tooltip {
  position: relative;

  .tooltiptext {
    font-size: 1em;
    line-height: 1em;
    text-align: center;
    color: $lcg-1;
    background-color: $dcg-11;

    @include centering-flex;
    visibility: hidden;
    position: absolute;
    top: 130%;
    left: 50%;
    transform: translateX(-50%);
    padding: 1em;
    border-radius: 5px;
    z-index: 1;

    // Fade in tooltip
    opacity: 0;
    transition: opacity 0.4s ease;

    &::after {
      // *** triangle variant 1 ***
      // content: "";
      // position: absolute;
      // top: 0;
      // left: 50%;
      // transform: translate(-50%, -85%);

      // width: 0;
      // height: 0;
      // border-bottom: 13px * 0.87 solid $dcg-11;
      // border-left: 13px * 0.5 solid transparent;
      // border-right: 13px * 0.5 solid transparent;
      // border-top-width: 0;

      // *** triangle variant 2 ***
      content: "";
      position: absolute;
      top: 0;
      left: 50%;
      transform: translate(-50%, -90%);
      border-width: 7px;
      border-style: solid;
      border-color: transparent transparent $dcg-11 transparent;
    }
  }

  &:hover {
    .tooltiptext {
      visibility: visible;
      opacity: 1;
    }
  }
}
