.breadcrumbs {
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
  margin-bottom: 2.5em;
  padding-top: 50px;

  li {
    display: flex;

    &:not(:last-child) {
      margin-right: 0.6em;

      &::after {
        content: "/\00a0";
        margin-left: 0.6em;
      }
    }
  }

  a {
    position: relative;
    width: max-content;
    padding-bottom: 0.2em;
    overflow: hidden;

    &:not([href]) {
      font-weight: bold;
      cursor: auto;
    }
  }

  a[href] {
    &::before {
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 0.05em;
      background-color: $lcg-1;
      transform: translate3d(-110%, 0, 0);
      transition: transform 0.3s ease;
    }

    &:focus,
    &:hover {
      &::before {
        transform: translate3d(0, 0, 0);
      }
    }

    &:active {
      opacity: 0.7;
    }
  }
}
