html {
  font-size: 10px;
  line-height: 1.15;
  height: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Helvetica, Arial, sans-serif;
  font-family: system-ui; // Only supported on Chromium-based browsers and Safari
  font-size: 1.4rem;
  line-height: inherit;
  color: $lcg-1;
  overflow-wrap: break-word;

  background-color: $mcg-4;
  background-image: url("../img/smile.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-attachment: fixed;

  @include zero-edges;
  min-width: $mobile-width;
  min-height: 100vh;

  @include column-flex;

  @media(max-width: $desktop-width) {
    background-size: 25%;
  }

  @media(max-width: $pre-desktop-width) {
    background-size: 50%;
  }
}

.visually-hidden {
  @include visually-hidden;
}

.list {
  @include zero-edges;
  list-style: none;
}

.button {
  border: none;
  outline: none;
}

.link {
  @include link-base-style;
}

.link,
button {
  cursor: pointer;
}

.container {
  @include centering-margin;
  width: 100%;
  max-width: 1461px;
  padding: 0 25px;

  @media (max-width: $pre-tablet-width) {
    padding: 0 17px;
  }
}

.scroll-stop {
  height: 100vh;
  overflow: hidden;
}
