.toggle {
  @media(max-width: $pre-desktop-width) {
    font-family: inherit;
	  font-size: 100%;
	  line-height: 1.15;
    color: $lcg-1;
    text-transform: none;

	  position: absolute;
	  top: 0;
	  right: 0;

    z-index: 2;

    @include centering-flex;

	  box-sizing: content-box;
	  width: 32px;
	  height: 32px;

    margin: 0;
	  padding: 10px;
	  background: 0 0;
	  border: 0;
    border-radius: 5px;
    transition: background 0.3s;
  }

  &:hover,
  &:focus {
    @media(max-width: $pre-desktop-width) {
      background: $mcg-10;
    }
  }
}

.toggle__description {
  @include visually-hidden;
}

.toggle__line {
  @media(max-width: $pre-desktop-width) {
    position: relative;
    display: inline-block;
    width: 30px;
    height: 3px;
    vertical-align: bottom;
    background: $lcg-1;
    transition: background 0.3s 0.5s;
  }

  &::before,
  &::after {
    @media(max-width: $pre-desktop-width) {
      content: "";
      position: absolute;
      left: 0;
      width: 100%;
      height: 3px;
      background: $lcg-1;
      transform-origin: 50% 50%;
      transition: top 0.3s 0.3s, transform 0.3s;
    }
  }

  &::before {
    @media(max-width: $pre-desktop-width) {
      top: 10px;
    }
  }

  &::after {
    @media(max-width: $pre-desktop-width) {
      top: -10px;
    }
  }
}

