@keyframes topline {
  0% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(32px);
  }

  100% {
    transform: translateY(32px) rotate(45deg);
  }
}

@keyframes middleline {
  50%,
  100% {
    stroke-width: 0;
  }
}

@keyframes bottomline {
  0% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(-32px);
  }

  100% {
    transform: translateY(-32px) rotate(-45deg);
  }
}


@keyframes topline-reverse {
  0% {
    transform: translateY(32px) rotate(45deg);
  }

  50% {
    transform: translateY(32px) rotate(0);
  }

  100% {
    transform: translateY(0);
  }
}

@keyframes middleline-reverse {
  0% {
    stroke-width: 0;
  }

  50%,
  100% {
    stroke-width: 4px;
  }
}

@keyframes bottomline-reverse {
  0% {
    transform: translateY(-32px) rotate(-45deg);
  }

  50% {
    transform: translateY(-32px) rotate(0);
  }

  100% {
    transform: translateY(0);
  }
}
