.dropdown {
  position: relative;
  transition-duration: 0.5s;

  &:focus-within > .dropdown__list {
    @include block-visibility;
  }
}

.dropdown__trigger {
  font-size: inherit;
  line-height: inherit;

  color: inherit;
  background-color: inherit;

  &:hover,
  &:focus {
    color: $mcg-9;
  }

  &:hover + .dropdown__list,
  &:focus + .dropdown__list {
    @include block-visibility;
  }
}

.dropdown__list {
  color: $mcg-4;
  background-color: $lcg-1;

  position: absolute;
  left: 0;
  min-width: 5rem;
  margin-top: 1rem;
  padding: 0.5em;
  border-radius: 5px;

  visibility: hidden;
  opacity: 0;
  transition: all 0.5s ease;

  @media(max-width: $pre-tablet-width) {
    top: 0;
    left: 100%;
    margin-top: 0;
  }

  &:hover,
  &:focus {
    @include block-visibility;
  }
}

.dropdown__item {
  width: 100%;

  &:not(:last-child) {
    margin-bottom: 0.5em;
  }
}

.dropdown__link {
  text-decoration: none;
  width: max-content;

  &:hover,
  &:focus-within {
    color: $mcg-9;
  }
}
