.navigation {
  color: inherit;

  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);

  width: 50%;
  margin-bottom: 20px;
  padding-top: 50px;

  @media(max-width: $pre-tablet-width) {
    position: static;
    width: 100%;
    transform:none;
  }

  &--js {
    @media(max-width: $pre-desktop-width) {
      color: $mcg-4;
      background: transparent;
      left: 100%;
      transform: translateX(-100%);
      z-index: 1;

      width: 32px;
      height: 32px;
      margin-bottom: 0;
      padding: 1em;
      transition: width 0.3s, height 0.3s, background 0.3s;

      border-top-left-radius: 5px;
      border-bottom-left-radius: 5px;
    }

    @media(max-width: $pre-tablet-width) {
      position: absolute;
    }

    @media(max-width: 321px) {
      border-top-left-radius: 0;
      border-bottom-left-radius: 0;
    }

    .navigation__list {
      @media(max-width: $pre-desktop-width) {
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;
        flex-wrap: nowrap;

        height: calc(100% - 32px);
        margin-top: 32px;
        overflow-y: auto;
      }
    }

    .navigation__item {
      @media(max-width: $pre-desktop-width) {
        flex-grow: 0;
      }

      &:not(:last-child) {
        @media(max-width: $pre-desktop-width) {
          margin-bottom: 10px;
        }
      }
    }

    .dropdown__list {
      @media(max-width: $pre-desktop-width) {
        color: $lcg-1;
        background-color: $mcg-4;
        top: 0;
        left: 100%;
      }
    }
  }
}

.navigation__list {
  display: flex;
  flex-wrap: wrap;

  @media(max-width: $pre-tablet-width) {
    flex-direction: column;
    align-items: flex-start;
  }
}

.navigation__item {
  font-size: 1.5em;
  line-height: inherit;

  flex-grow: 1;

  &:not(:last-child) {
    margin-right: 10%;

    @media(max-width: $pre-tablet-width) {
      margin-right: 0;
      margin-bottom: 10px;
    }
  }
}
